.product-container {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: auto;
  margin: 5px;
  .product-details {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: flex-end;
    .decription-product {
      font-size: 0.9em;
      padding: 5px;
      height: auto;
      width: 100%;
      background-color: grey;
      position: relative;
      opacity: 80%;
      color: rgb(35, 39, 42);
      z-index: 99;
    }
    .product-figure {
      box-shadow: 1px 2px 4px 1px rgba(215, 215, 215, 0.71);
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      align-items: center;
      .img-product {
        width: 100%;
        height: auto;
        align-items: center;
      }
    }

    .quantity-product {
      font-size: 0.9em;
      color: rgb(35, 39, 42);
      background-color: rgb(189, 189, 189);
      width: 110px;
      height: auto;
      padding: 5px;
      opacity: 80%;
      position: relative;
      z-index: 99;
      top: 50px;
      right: 0px;
    }
  }
}

