@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;background-color:#fffbf8;color:#23272a;font-weight:300}.modal-efecto{transition:left 300ms linear}button{text-decoration:none}

.Main-container{width:100vw;display:flex;flex-direction:column;justify-content:center;align-items:center;align-content:space-around}

.Main{margin:0px}.Main .Main-header{width:100vw;height:80px;background-color:#f3e7e0;border-radius:0px 0px 50px 50px;box-shadow:1px 2px 4px 1px rgba(215,215,215,0.71)}.Main .Main-header .header-title{font-size:18px;letter-spacing:3px;text-align:center;padding-top:28px;font-weight:400}


.search{width:100vw;margin:0px;padding:0px}.search .search-title{font-size:16px;font-weight:lighter;text-align:center}.search .search-title:nth-child(1){margin-top:20px}.search-container{display:flex;justify-content:center;margin-top:25px}.search-container .search-container-icon{font-size:21px;background-color:transparent;border:none;outline:none;color:#dcddde}.search-container .search-container-input{padding:5px 10px;background-color:transparent;border:1px solid;font-size:16px;width:200px;border-radius:4px;color:#dcddde;margin-left:10px}

.btn-container{width:100vw;display:flex;flex-direction:center;justify-content:center;margin-top:20px}.btn-container .btn{width:80%;height:50px;border:1px solid #8b632a;border-radius:4px;background-color:#bd8436}.btn-container .btn .btn-title{color:#503714}.btn-container .btn .btn-text{color:#503714}

.container-product{margin:0px;display:flex;flex-direction:column;align-items:rigth;padding:0px 22px;height:100vh}

.product-view{margin:20px 0px 0px}.product-view .main-product{width:100vh;height:200px;z-index:2px;box-shadow:0px 0px 5px}.product-view .title-product{position:relative;left:0px;bottom:50px;z-index:20px;background-color:#757575;color:white;width:67%;padding:5px;border-radius:0px 17px 17px 0px}

.product-name{margin:0px;color:#424242}.product-name h1{font-size:23px}.product-name h2{font-size:18px;font-weight:100}.product-descrption,.product-photo-descrption{margin:20px 0px}.product-descrption .product-descrption-title,.product-photo-descrption .product-descrption-title{font-size:16px;padding:7px 0px;border-bottom:1px solid #9b8e8e}.product-descrption p,.product-photo-descrption p{margin-top:10px;text-align:justify}.product-photo-descrption{margin:20px 0px}.product-photo-descrption .product-size{margin:20px 0px;display:flex;flex-direction:row;justify-content:left}.product-photo-descrption .product-size img{width:150px;height:150px;margin-right:40px}.product-photo-descrption .product-size .detail-size li{line-height:30px}.product-photo-descrption .product-size .detail-size li:nth-child(1){list-style:none}

.dowload{display:flex;flex-direction:column;justify-content:center;text-align:center;margin:0px}.dowload p{margin:10px 30px}.dowload button{padding:8px;margin:7px 50px;font-size:17px;background-color:#FFE082;border-radius:4px;border:none;cursor:pointer}.dowload button:hover{background-color:#cfc4a3}.dowload button:focus,.dowload button :active{outline:none}.dowload button .icon-arrow{font-size:18px;margin-right:8px}

.productView-container{width:90vw;margin-top:20px}.productView-container .product-title{margin:0;font-size:14px;line-height:21px;letter-spacing:1px;text-align:center;margin-bottom:10px}.productView-container .product-title h1{font-weight:lighter}.btn-back{width:55%;font-size:12px;height:40px;border:1px solid #a57736;border-radius:4px;background-color:#bd8436;position:fixed;z-index:99;bottom:10px;color:white}.btn-back .icon{width:15px;height:15px;padding:3px;border-radius:50px;background-color:white;color:#bd8436;margin-right:5px}

.ProductList-Container{display:flex;flex-direction:row;justify-content:space-around;align-items:flex-start;flex-wrap:wrap}

.product-container{display:flex;flex-direction:column;width:90%;height:auto;margin:5px}.product-container .product-details{display:flex;flex-direction:column;width:100%;height:100%;align-items:flex-end}.product-container .product-details .decription-product{font-size:0.9em;padding:5px;height:auto;width:100%;background-color:grey;position:relative;opacity:80%;color:#23272a;z-index:99}.product-container .product-details .product-figure{box-shadow:1px 2px 4px 1px rgba(215,215,215,0.71);display:flex;flex-direction:column;width:100%;height:100%;align-items:center}.product-container .product-details .product-figure .img-product{width:100%;height:auto;align-items:center}.product-container .product-details .quantity-product{font-size:0.9em;color:#23272a;background-color:#bdbdbd;width:110px;height:auto;padding:5px;opacity:80%;position:relative;z-index:99;top:50px;right:0px}

.overlay{position:fixed;left:0;top:0;background-color:#f3e7e0;width:100vw;height:100vh;padding:10px;margin:0;z-index:100}.modal{width:600px;z-index:100;position:fixed;left:calc(50vw - 300px);top:30vw;box-sizing:border-box}.modal header{display:flex;flex-direction:column;justify-content:space-between;align-items:center;text-align:center}.modal header h2{margin:0;letter-spacing:5px;padding:10px;text-align:center}.modal header p{font-size:20px;letter-spacing:3px}.modal header p:nth-child(1){margin-left:-103px}.modal header img{position:relative;width:180px;top:200px}.loader{visibility:visible;opacity:1;transition:visibility 0.3s linear,opacity 0.3s linear}.loader.hidden{visibility:hidden;opacity:0}

